<template>
  <form @submit.prevent="handleSubmit" class="container mt-4">
    <!-- Name Input -->
    <div class="mb-3">
      <label for="name" class="form-label">Name:</label>
      <input type="text" id="name" v-model="name" class="form-control" placeholder="Enter your name" required>
    </div>

    <!-- Email Input -->
    <div class="mb-3">
      <label for="email" class="form-label">Email:</label>
      <input type="email" id="email" v-model="email" class="form-control" placeholder="Enter your email" required>
    </div>

    <!-- Checkbox for Newsletter Subscription -->
    <div class="mb-3 form-check">
      <input type="checkbox" id="newsletter" v-model="subscribe" class="form-check-input">
      <label for="newsletter" class="form-check-label">Subscribe to Newsletter</label>
    </div>

    <!-- Radio Buttons for Contact Reason -->
    <div class="mb-3">
      <label class="form-label">Reason for Contacting:</label>
      <div class="form-check">
        <input type="radio" id="support" value="Support" v-model="contactReason" class="form-check-input">
        <label for="support" class="form-check-label">Support</label>
      </div>
      <div class="form-check">
        <input type="radio" id="sales" value="Sales" v-model="contactReason" class="form-check-input">
        <label for="sales" class="form-check-label">Sales</label>
      </div>
    </div>

    <!-- Select Input with Dynamic Options -->
    <div class="mb-3">
      <label for="topic" class="form-label">Select a Topic:</label>
      <select id="topic" v-model="topic" class="form-control">
        <option disabled value="">Please select one</option>
        <option v-for="option in options" :key="option" :value="option">{{ option }}</option>
      </select>
    </div>

    <!-- Numeric Input -->
    <div class="mb-3">
      <label for="quantity" class="form-label">Quantity (Numbers only):</label>
      <input type="number" id="quantity" v-model.number="quantity" class="form-control" placeholder="0">
    </div>

    <button type="submit" class="btn btn-primary">Submit</button>
  </form>
</template>

<script>
import { ref } from 'vue';

export default {
  name: 'ContactForm',
  setup() {
    const name = ref('');
    const email = ref('');
    const subscribe = ref(false);
    const contactReason = ref('');
    const topic = ref('');
    const options = ref(['Customer Service', 'Return Policy', 'Product Inquiry']);
    const quantity = ref(0);
    const formSubmitted = ref(false);

   const handleSubmit = () => {
  formSubmitted.value = true;

  alert(`Thank you, ${name.value}. We have received your submission and will contact you soon!`);
};


    return { name, email, subscribe, contactReason, topic, options, quantity, formSubmitted, handleSubmit };
  }
}
</script>

<style scoped>
.container {
  max-width: 600px;
  margin: auto;
  padding: 20px;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 15px rgba(0,0,0,0.1);
}

.form-label {
  font-weight: bold;
}

.form-control {
  box-sizing: border-box; /* Ensures padding doesn't affect width */
  height: 38px; /* Standard height to align with design systems */
}

.form-check-label {
  margin-left: 0.5rem; /* Spacing after checkbox or radio button */
}

.btn-primary {
  width: 100%; /* Makes button full width of form */
  padding: 10px; /* Larger, more clickable area */
  margin-top: 10px; /* Spacing from last form element */
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .container {
    padding: 15px;
  }
}
</style>
