
vue
<template>
  <header :class="['header', { 'dark-header': isDarkMode }]">
    <nav class="navbar navbar-expand-lg" :class="['navbar-light', { 'navbar-dark': isDarkMode }]">
      <div class="container-fluid">
        <router-link to="/" class="navbar-brand">
          <img :src="require('@/assets/1.jpeg')" alt="Art Store Logo" style="height: 50px;">
        </router-link>
        <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarNav">
          <ul class="navbar-nav me-auto mb-2 mb-lg-0">
            <li class="nav-item">
              <router-link to="/" class="nav-link" exact-active-class="active">Home</router-link>
            </li>
            <li class="nav-item">
              <router-link to="/gallery" class="nav-link">Gallery</router-link>
            </li>
            <li class="nav-item">
              <router-link to="/artists" class="nav-link">Artists</router-link>
            </li>
            <li class="nav-item">
              <router-link to="/contact" class="nav-link">Contact Us</router-link>
            </li>
          </ul>
          <form class="d-flex" @submit.prevent="onSearch">
            <input class="form-control me-2" type="search" placeholder="Search" aria-label="Search" v-model="searchQuery">
            <button class="btn btn-outline-success" type="submit">Search</button>
          </form>
          <button class="btn btn-outline-dark ms-2" @click="toggleDarkMode">
            <span v-if="isDarkMode">Toggle Light Mode</span>
            <span v-else>Toggle Dark Mode</span>
          </button>
        </div>
      </div>
    </nav>
  </header>
</template>

<script>
import { computed } from 'vue';
import { useDarkModeStore } from '../stores/darkMode';

export default {
  name: 'WebsiteHeader',
  data() {
    return {
      searchQuery: '',
      logoImage: '1.jpeg', // Ensure this image path is correct
    };
  },
  setup() {
    const darkModeStore = useDarkModeStore();
    const isDarkMode = computed(() => darkModeStore.isDarkMode);

    function toggleDarkMode() {
      darkModeStore.toggleDarkMode();
    }

    return {
      toggleDarkMode,
      isDarkMode,
    };
  },
  methods: {
    onSearch() {
      alert(`Search for ${this.searchQuery}`); // Implement real search functionality as needed
    },
  },
};
</script>

<style scoped>
.header {
  transition: background-color 0.3s, color 0.3s;
}

.dark-header {
  background-color: #333;
  color: #fff;
}

.dark-header .navbar-dark .navbar-nav .nav-link {
  color: #fff;
}

.dark-header .navbar-dark .navbar-nav .nav-link:hover {
  color: #ccc;
}

.dark-header .btn-outline-dark {
  color: #fff;
  border-color: #fff;
}

.dark-header .btn-outline-dark:hover {
  color: #333;
  background-color: #fff;
  border-color: #fff;
}
</style>