import { createRouter, createWebHistory } from 'vue-router';
import Home from '../views/HomeView.vue';
import Gallery from '../views/Gallery.vue';
import BuyView from '../views/BuyView.vue';
import ContactPage from '../views/ContactPage.vue';
import ClassicArtPage from '../views/ClassicArtPage.vue';
import ContemporaryArtPage from '../views/ContemporaryArtPage.vue';
import AbstractPage from '../views/AbstractPage.vue';
import ArtistsPage from '../views/ArtistsPage.vue';




const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes: [
    { path: '/', name: 'Home', component: Home },
    { path: '/gallery', name: 'Gallery', component: Gallery },
    { path: '/buy/:artId',name: 'Buy',component: BuyView},
    { path: '/buy',name: 'Buy',component: BuyView},
    { path: '/contact', name: 'Contact', component: ContactPage },
    { path: '/classicart', name: 'ClassicArt', component: ClassicArtPage },
    { path: '/artists', name: 'Artists', component: ArtistsPage },
    { path: '/abstractPage', name: 'AbstractPage', component: AbstractPage },
    { path: '/ContemporaryArtPage', name: 'ContemporaryArtPage', component: ContemporaryArtPage },
  ]
});

export default router;
