<template>
  <div class="container">
    <h2>Checkout Form</h2>
    <p>Please enter your details below to complete your purchase.</p>

    <form @submit.prevent="validateForm">
      <div class="mb-3">
        <label for="name" class="form-label">Name:</label>
        <input type="text" class="form-control" id="name" v-model="form.name" placeholder="Your name" required @input="validateName" />
        <div id="nameError" class="form-text">{{ errors.name }}</div>
      </div>
      <div class="mb-3">
        <label for="email" class="form-label">Email:</label>
        <input type="email" class="form-control" id="email" v-model="form.email" placeholder="Enter email" required @input="validateEmail" />
        <div id="emailError" class="form-text">{{ errors.email }}</div>
      </div>
      <div class="mb-3">
        <label for="cardDetails" class="form-label">Card Details:</label>
        <input type="text" class="form-control" id="cardDetails" v-model="form.cardDetails" placeholder="Card Number" minlength="16" maxlength="16" required @input="validateCardDetails" />
        <div id="cardError" class="form-text">{{ errors.cardDetails }}</div>
      </div>
      <div class="mb-3">
        <label for="address" class="form-label">Address:</label>
        <input type="text" class="form-control" id="address" v-model="form.address" placeholder="Shipping Address" required @input="validateAddress" />
        <div id="addressError" class="form-text">{{ errors.address }}</div>
      </div>
      <button type="submit" class="btn btn-primary">Complete Purchase</button>
      <button type="reset" class="btn btn-secondary" @click="resetForm">Reset</button>
    </form>
  </div>
</template>

<script>
export default {
  name: 'CheckoutPage',
  data() {
    return {
      form: {
        name: '',
        email: '',
        cardDetails: '',
        address: ''
      },
      errors: {
        name: '',
        email: '',
        cardDetails: '',
        address: ''
      }
    };
  },
  methods: {
    validateName() {
      this.errors.name = this.form.name ? '' : 'Name is required.';
    },
    validateEmail() {
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      this.errors.email = !this.form.email ? 'Email is required.' : !emailRegex.test(this.form.email) ? 'Invalid email format.' : '';
    },
    validateCardDetails() {
      this.errors.cardDetails = this.form.cardDetails.length === 16 ? '' : 'Card number must be exactly 16 digits.';
    },
    validateAddress() {
      this.errors.address = this.form.address ? '' : 'Address is required.';
    },
    validateForm() {
      this.validateName();
      this.validateEmail();
      this.validateCardDetails();
      this.validateAddress();
      if (!this.errors.name && !this.errors.email && !this.errors.cardDetails && !this.errors.address) {
        alert('Form submitted successfully!');
        this.resetForm();
      } else {
        alert('Please correct the errors before submitting.');
      }
    },
    resetForm() {
      this.form = {
        name: '',
        email: '',
        cardDetails: '',
        address: ''
      };
      this.errors = {
        name: '',
        email: '',
        cardDetails: '',
        address: ''
      };
    }
  }
};
</script>

<style scoped>
/* Scoped styles for the Checkout Page */
.container {
  margin-top: 30px;
}
</style>
